/**
 * åˆ¤æ–­ä¸€ä¸ªå€¼æ˜¯å¦ä¸ºå¸ƒå°”ç±»å‹çš„å­—ç¬¦ä¸²
 * @param value è¦æ£€æŸ¥çš„å€¼
 * @returns å¦‚æœæ˜¯å¸ƒå°”ç±»å‹å­—ç¬¦ä¸²è¿”å› trueï¼Œå¦åˆ™è¿”å› false
 */
export function isBooleanString(value: any): boolean {
    if (typeof value !== 'string') {
        return false
    }
    
    const lowerValue = value.toLowerCase().trim()
    const truthy = ['true', 'yes', '1', 'on', 'enabled', 'active']
    const falsy = ['false', 'no', '0', 'off', 'disabled', 'inactive']
    
    return truthy.includes(lowerValue) || falsy.includes(lowerValue)
}

/**
 * å°†å¸ƒå°”ç±»å‹å­—ç¬¦ä¸²è½¬æ¢ä¸ºå¸ƒå°”å€¼
 * @param value è¦è½¬æ¢çš„å€¼
 * @returns è½¬æ¢åçš„å¸ƒå°”å€¼ï¼Œå¦‚æœä¸æ˜¯å¸ƒå°”ç±»å‹å­—ç¬¦ä¸²åˆ™è¿”å›åŸå€¼
 */
export function convertBooleanString(value: any): any {
    if (!isBooleanString(value)) {
        return value
    }
    
    const lowerValue = value.toLowerCase().trim()
    const truthy = ['true', 'yes', '1', 'on', 'enabled', 'active']
    
    return truthy.includes(lowerValue)
}

/**
 * å¤„ç† frontMatter å¯¹è±¡ï¼Œå°†å…¶ä¸­çš„å¸ƒå°”ç±»å‹å­—ç¬¦ä¸²è½¬æ¢ä¸ºå¸ƒå°”å€¼
 * @param frontMatter frontMatter å¯¹è±¡
 * @returns å¤„ç†åçš„ frontMatter å¯¹è±¡
 */
export function processFrontMatterBooleans(frontMatter: Record<string, any>): Record<string, any> {
    const processed = {}
    
    Object.keys(frontMatter).forEach(key => {
        processed[key] = convertBooleanString(frontMatter[key])
    })
    
    return processed
}

/**
 * æ ¼å¼åŒ– frontMatter å€¼ç”¨äºæ˜¾ç¤º
 * @param value frontMatter çš„å€¼
 * @returns æ ¼å¼åŒ–åçš„æ˜¾ç¤ºå­—ç¬¦ä¸²
 */
export function formatFrontMatterValue(value: any): string {
    // å¦‚æœå€¼ä¸º null æˆ– undefinedï¼Œæ˜¾ç¤º 'unset'
    if (value === null || value === undefined) {
        return 'unset'
    }
    
    // å¦‚æœå€¼ä¸ºç©ºå­—ç¬¦ä¸²ï¼Œæ˜¾ç¤º 'empty'
    if (value === '') {
        return 'empty'
    }
    
    // å¦‚æœå€¼ä¸ºå¸ƒå°”ç±»å‹ï¼Œè½¬æ¢ä¸ºå­—ç¬¦ä¸²
    if (typeof value === 'boolean') {
        return value.toString()
    }
    
    // å…¶ä»–æƒ…å†µç›´æ¥è½¬æ¢ä¸ºå­—ç¬¦ä¸²
    return String(value)
} 